<template>

  <TaskCard less="5" title="Переворот слова" v-bind:tech="['JavaScript']" level="Средний" v-if="view === 'card'"/>

  <ul v-if="view === 'note'">
    <Link to="https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/reverse"><img src="@/assets/mozilla.png" height="24" alt="">Array.reverse()</Link>
    <Link to="https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/String/split"><img src="@/assets/mozilla.png" height="24" alt="">String.split()</Link>
    <Link to="https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/join"><img src="@/assets/mozilla.png" height="24" alt="">Array.join()</Link>
  </ul>

  <div className="do" v-if="view === 'do'">
    <span>Вам нужно разработать функцию reverse() для реверса слова<br>Примеры:</span>
    <ul>
      <li>reverse("Hello") => "olleH"</li>

    </ul>
  </div>

  <div className="container task_view" v-if="view === 'task'">
    <CodeRunner :code_v='code' :change-func="runnerTest" func-name="task()" :autotest="true"/>
  </div>
</template>

<script>
import Link from "@/components/Link";
import TaskCard from "@/components/TaskCard";
import CodeRunner from "@/components/CodeRunner";
import wlpRun from "@/coderunner/wlpRunner";


export default {
  name: "AlertT",
  components: {
    Link,
    TaskCard,
    CodeRunner
  },
  props: {
    view: {
      type: String
    }
  },
  data() {
    return {
      code : `function reverse(word) {

      }`
    }
  },
  methods: {
    runnerTest(code, autoTest = false){

      let config = {
        autoTest: {
          enabled : autoTest,
          target : "reverse",
          originalFunction(word){
            return word.split("").reverse().join("");
          },
          logic : `
        function makeid(length) {
            let result = '';
            const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
            const charactersLength = characters.length;
            let counter = 0;
            while (counter < length) {
              result += characters.charAt(Math.floor(Math.random() * charactersLength));
              counter += 1;
            }
            return result;
        }
        let word = makeid(5)
        if(reverse(word) === originalFunction(word)){
          WLP_COMPLETE_RAND(WLP_TEST_NUM)
        }
        else{
          WLP_FAIL_RAND(WLP_TEST_NUM);
        }
      `,
          testsCount: 10
        }
      }
      wlpRun(code,config).then(()=>{

      })

    }
  },
  mounted() {
    if (this.view === "task") {
      return
    }
  }
}
</script>

<style scoped>
.task_view {
  padding: 7vh 0;
}
</style>