<template>
  <div class="wlp-ui_runner" >

    <div class="footer" v-if="autotest" ref="footer">
      <span>Введёный код проходит проверку. Функция <span class="runner-function-name">{{funcName}}</span> будет вызываться автоматически</span>
      <i class="fa-solid close-footer" @click="$refs.footer.classList.add('hidden')">X</i>
    </div>
    <div class="wlpRunner">
      <Codemirror
          v-model:value="code"
          :options="cmOptions"
          border
          placeholder="test placeholder"
          :height="200"
          style="font-size: 1em"
      />
      <div class="console"></div>
    </div>
    <div class="actions">
      <button @click="changeFunc(code,false)">Выполнить (Ctrl + S)</button>
      <button @click="changeFunc(code,true)" class="autotest" v-if="autotest">Запустить тест (Ctrl + Enter)</button>
    </div>
  </div>
</template>

<script>

import Codemirror from "codemirror-editor-vue3";

// placeholder
import "codemirror/addon/display/placeholder.js";

// language
import "codemirror/mode/javascript/javascript.js";
// placeholder
import "codemirror/addon/display/placeholder.js";
import "codemirror/addon/hint/show-hint.css";
import "codemirror/addon/hint/show-hint";
import "codemirror/addon/hint/javascript-hint";
// theme
import "codemirror/theme/dracula.css";

import { ref } from "vue";
export default {
  name: "CodeRunner",
  components: { Codemirror },
  props : {
    code_v : {
      type : String,
      default : ""
    },
    autotest : {
      type : Boolean,
      default: false
    },
    changeFunc : {
      type : Function,
    },
    funcName : {
      type : String
    }

  },
  setup(props){
    const code = ref(`${props.code_v}`);

    return {
      code,
      cmOptions: {
        mode: "text/javascript", // Language mode
        theme: "dracula", // Theme,
        placeholder: "// Пишите код",
        autocomplete: true,
        extraKeys: {
          Ctrl: 'autocomplete',
        },
        indentWithTab: true,
      },
      methods : {

      }
    };
  },
  mounted() {
    document.addEventListener('keydown', (ev) => {
      if (ev.code == 'KeyS' && ev.ctrlKey) {
        ev.preventDefault();
        this.changeFunc(this.code);
      }
      if (ev.code == 'Enter' && ev.ctrlKey) {
        ev.preventDefault();
        this.changeFunc(this.code,true);
      }
    });
  }
};
</script>

<style scoped lang="scss">


@import "@/assets/styles.scss";
  .wlp-ui_runner{
    display: flex;
    flex-direction: column;
  }
.autotest{
  background: #509a14;
  &:hover{
    background: #3e7a0d;
  }

}
  .wlpRunner{
    height: 100%;
    position: relative;
    display: flex;
    align-items: flex-start;
  }
  .footer{
    background: #c26d13;
    padding: 10px;
    border-top: 1px solid #1a1a1a;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: opacity .3s linear 0s;
  }
  .footer span{
    color: $color-hint;
    font-weight: bold;
  }
  .hidden{
    opacity: 0;
  }
  .close-footer{
    color: #fff;
    font-family: Consolas;
  }
</style>