function wlpCodeFixer(code){
    code = code.replaceAll("console.log","WLPEngine.log");
    code = code.replaceAll("alert","WLPEngine.alert");
    code = code.replaceAll("prompt","WLPEngine.prompt");
    code = code.replaceAll("document.querySelector","WLPEngine.querySelector");
    return code;
}
window.WLPEngine = {
    META : [],
    metaParse(meta = [],type){
        let found = []
        meta.forEach((i)=>{
            if(i.type === type) found.push(i)
        })
        return found;
    },
    log(message){
        this.META.push(`console.log(${message})`);
        let consoleWrapper = document.querySelector(".console");
        let log = document.createElement("p");
        log.classList.add("pconsole");
        log.innerHTML = `[INFO] ${message}`;
        consoleWrapper.append(log);
    },
    error(message){
        let consoleWrapper = document.querySelector(".console");
        let log = document.createElement("p");
        log.classList.add("pconsole");
        log.classList.add("c-error");
        log.innerHTML = `[ERROR] ${message}`;
        consoleWrapper.append(log);
    },
    alert(message){
        this.META.push({
            type : "alert",
            message : message
        });
        alert(message)
    },
    prompt(message){
        let answer = prompt(message)
      let META = {
          type : "prompt",
          question : message,
          answer : answer
      }
      this.META.push(META);
    },
    print(m,t){
        let consoleWrapper = document.querySelector(".console");
        let log = document.createElement("p");
        log.classList.add("pconsole");
        log.classList.add("c-" + t);
        log.innerHTML = `${m}`;
        consoleWrapper.append(log);

    },
    querySelector(selector){
        this.META.push({
            type : "querySelector",
            selector : selector,
            resultNode : document.querySelector(selector)
        });
        if(document.querySelector(selector) !== null) document.querySelector(selector).wlp_selected = true;
        return document.querySelector(selector);
    }
}
function wlpRun(code, params = {
    autoTest: {
        enabled : false,
        originalFunction() {
            return;
        }
    }
}){
    document.querySelector(".console").innerHTML = ''
    const PREINJECT = `
    let WLP_RANDOM_TEST_COMPLETED = 0;
    let WLP_COMPLETE_RAND = (i) => { 
        WLP_RANDOM_TEST_COMPLETED++;
        window.WLPEngine.log("Рандом тест #" + i + " выполнен");
    }
    let WLP_FAIL_RAND = (i) => { 
        window.WLPEngine.log("Рандом тест #" + i + " провален. ");
    }
    
    `


    code = `${wlpCodeFixer(code)}\n`;


    if(params.autoTest.enabled){
        code += `
        
        function _wlp__inject(){ 
        
        function ${params.autoTest.originalFunction.toString()}
        
        ${PREINJECT}
        
    if(typeof ${params.autoTest.target} !== "undefined"){
        for(let WLP_TEST_NUM = 1; WLP_TEST_NUM <= ${params.autoTest.testsCount}; WLP_TEST_NUM++){
          ${params.autoTest.logic}
        }
        if(WLP_RANDOM_TEST_COMPLETED == ${params.autoTest.testsCount}) window.WLPEngine.print("Задача выполнена","success");
        else window.WLPEngine.print("Задача провалена","error");
    }
    else{
        window.WLPEngine.print("Функция ${params.autoTest.target} не существует","error");
    }
         
 }\n_wlp__inject()`
    }


    return new Promise((resolve,reject)=>{
        try {
            let task = new Function(code);
            task();

            resolve(window.WLPEngine.META);

            window.WLPEngine.META = [];
            document.querySelector(".console").scrollTo(0,document.querySelector(".console").scrollHeight);
        }
        catch (e) {
            window.WLPEngine.error(e);
            reject(e);
            document.querySelector(".console").scrollTo(0,document.querySelector(".console").scrollHeight);
        }
    })
}

export default wlpRun;